import * as React from "react"
import Layout from "../components/Layout"
import Seo from "../components/Seo";
import { graphql, Link, PageProps } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
// import { StaticImage } from "gatsby-plugin-image";

/* 정적 홈페이지에 유용(갯츠비): blog, e-commerce */
export default function IndexPage({data}:PageProps<Queries.StickersQuery>){
  return (
  <Layout title="DevStickers">
    <div className="grid">
  {/* <StaticImage 
  alt="stickers"
  src="https://images.unsplash.com/photo-1495012379376-194a416fcc5f?q=80&w=1025&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D"></StaticImage> */}
  {data.allContentfulStickerPack.nodes.map((sticker)=>(
    <article key={sticker.name}>
      {sticker.preview?.gatsbyImageData ? (
        <GatsbyImage image={getImage(sticker.preview?.gatsbyImageData)!}
      alt={sticker.name!}/>
      ) : (
        <div>No image</div>
      )
    }
      <Link to={`/products/${sticker.id}`}>
      <h2>{sticker.name}</h2>
      <h4>{sticker.price}</h4>
      </Link>
    </article>
  ))}
  </div>
  </Layout>
  );
};
export const query = graphql`
  query Stickers {
  allContentfulStickerPack {
    nodes {
      id
      name
      price
      preview {
        gatsbyImageData(height: 450)
      }
    }
  }
}
`;
/* site name 바꾸는 Head */
export const Head = () => <Seo title="Home"></Seo>